@import "variables";
@import "mixin";
@import "deliveryPopup";
@import "fixed_nav";
@import "authorization";
@import "aside_buttons";

body {
  font-size: 20px;
  line-height: 22px;
  color: #474747;
  .header {
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: ". headline headline"
      "logo search cabinet"
      "navPanel navPanel navPanel";
      grid-template-columns: 25% 1fr 25%;
      .header__headline {
        grid-area: headline;
        p {
          font: 1em/2em @sansregular;
          color: #474747;
          text-align: center;
          background-color: #ececec;
          border-bottom-right-radius: 15px;
          border-bottom-left-radius: 15px;
        }
      }
      .header__logo {
        grid-area: logo;
        a{
          margin-left: 50px;
          display: block;
          width: 100%;
          height: 100%;
          background: url("../img/logo.png") no-repeat;
          background-size: contain;
        }
      }
      .header__mainSearch {
        grid-area: search;
        display: flex;
        flex-wrap: wrap;
        .search {
          width: 100%;
          margin-top: 70px;
          input {
            width: 90%;
            height: 45px;
            padding-left: 25px;
            font: 1.125em/45px @sansregular;
            color: #9e9e9e;
            border: 3px solid @mainColor;
            border-radius: 50px;
            outline: none;
          }
          i {
            font-size: 1.5em;
            position: relative;
            left: -50px;
            top: 2px;
            color: @mainColor;
          }
        }
        .numbers {
          margin-top: 50px;
          ul {
            display: flex;
            li {
              padding: 0 10px;
              i {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 65px;
                height: 65px;
                font-size: 2em;
                color: @mainColor;
                border: 3px solid @mainColor;
                border-radius: 50px;
              }
              a {
                font: 1.6em/2em @sansregular;
                color: #474747;
                text-decoration: none;
              }
            }
          }
        }
        .workTime {
          margin: 55px 0 0 60px;
          display: flex;
          i {
            font-size: 1.5em;
            line-height: 1.725em;
            color: @mainColor;
          }
          ul {
            margin-left: 20px;
            li {
              span {
                font: .7em/1em @sansregular;
              }
            }
          }
        }
      }
      .header__cabinet {
        grid-area: cabinet;
        margin-top: 70px;
        ul {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          li {
            a {
              display: flex;
              justify-content: space-around;
              align-content: center;
              text-decoration: none;
              span {
                display: inline-block;
                font: 1.125em/1.1em @sansregular;
                color: #474747;
                text-align: center;
                i {
                  padding: 0 20px;
                  font-size: 2em;
                  color: @mainColor;
                }
                &:hover {
                  text-decoration: underline;
                  cursor: pointer;
                }
              }
            }
            &:nth-child(1) {
              width: 45%;
            }
            &:nth-child(2) {
              width: 45%;
            }
            &:nth-child(3) {
              a{
                margin-top: 50px;
                width: 330px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                font: 1.125em/1.1em @sansregular;
                color: #474747;
                text-align: center;
                text-decoration: none;
                border: 1px solid @mainColor;
                border-radius: 5px;
                span{
                  margin: 5px 10px;
                }
                i{
                  padding: 3px;
                  font-size: 2em;
                  color: #fff;
                  background-color: #7d3daf;
                  border-radius: 5px;
                }
              }
            }
          }
        }
      }
      .header__nav-panel {
        grid-area: navPanel;
        &__major {
          ul {
            display: flex;
            justify-content: space-between;
            margin-top: 50px;
            li {
              width: 16.67%;
              a {
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @sansbold;
                color: #fff;
                text-align: center;
                text-decoration: none;
                background-color: #176975;
                border-right: 1px solid #77c9cc;
                border-left: 1px solid #77c9cc;
                &:hover {
                  background-color: @hoverColor;
                  transition: .5s;
                }
              }
            }
          }
        }
        &__goods {
          ul {
            display: flex;
            justify-content: space-between;
            li {
              width: 25%;
              .dropMenu {
                width: 1150px;
                position: absolute;
                z-index: 10;
                padding: 0 25px 25px 0;
                display: flex;
                flex-wrap: wrap;
                background-color: @hoverColor;
                li {
                  margin: 20px 0 0 25px;
                  width: 30%;
                  display: flex;
                  flex-direction: column;
                  a {
                    color: #fff;
                    text-align: left;
                    border: none;
                    background-color: @hoverColor;
                    &:hover {
                      text-decoration: underline;
                    }
                  }
                  &:nth-child(1) {
                    font: 1em/1.125em @sansbold;
                    i {
                      line-height: 1.125em;
                    }
                  }
                  ul {
                    display: flex;
                    flex-direction: column;
                    li {
                      margin: 0 0 0 25px;
                      width: 100%;
                      a {
                        font: 1em/1.125em @sansregular;
                      }
                    }
                  }
                }
              }
              a {
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @sansregular;
                color: @linkColor;
                text-align: center;
                text-decoration: none;
                background-color: #77c9cc;
                border: 1px solid @mainColor;
                &:hover {
                  color: #fff;
                  background-color: @hoverColor;
                  transition: .5s;
                }
              }
            }
          }
        }
      }
    }
  }
  .content {
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      margin-bottom: 100px;
      display: grid;
      grid-template-areas: "breadcrumb breadcrumb"
                           "busket order";
      grid-template-columns: 1fr 35%;
      .content__catalog__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__busket{
        grid-area: busket;
        margin-right: 25px;
        h5 {
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        ul{
          padding: 20px;
          border: 2px solid #e6e6e6;
          border-radius: 15px;
          li{
            margin: 40px 0;
            div{
              display: flex;
              justify-content: space-between;
              align-items: center;
              .value{
                width: 45%;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
              }
              .quantitu{
                span{
                  display: inline-block;
                  width: 25px;
                  height: 40px;
                  font: 1.375em/40px @sansregular;
                  text-align: center;
                  color: @linkColor;
                  background-color: #77c9cc;
                  user-select: none;
                  &:hover{
                    background-color: @mainColor;
                    cursor: pointer;
                    transition: .5s;
                  }
                }
                .minus{
                  border-top-left-radius: 5px;
                  border-bottom-left-radius: 5px;
                }
                .plus{
                  border-top-right-radius: 5px;
                  border-bottom-right-radius: 5px;
                }
                input{
                  width: 35px;
                  height: 34px;
                  font-size: 1em;
                  font-family: @sansregular;
                  text-align: center;
                  color: @linkColor;
                  border: 2px solid #77c9cc;
                  outline: none;
                }
              }
              .price{
                font: 1.375em/1.5em @sansbold;
                color: @linkColor;
                span{
                  font: .75em/.875em @sansregular;
                }
              }
              button{
                width: 125px;
                height: 40px;
                font: .875em/40px @sansregular;
                color: #fff;
                text-align: center;
                text-transform: uppercase;
                background-color: @mainColor;
                border: none;
                border-radius: 5px;
                outline: none;
                &:hover{
                  cursor: pointer;
                }
              }
              .delete{
                display: inline-block;
                font-size: 1.5em;
                color: @mainColor;
                &:hover{
                  cursor: pointer;
                }
              }
            }
          }
        }
        .total{
          margin-top: 25px;
          display: flex;
          h6{
            font: 1.125em/1.25em @sansbold;
            color: @linkColor;
          }
          p{
            margin-left: 50px;
            font: 1.125em/1.25em @sansbold;
            color: @linkColor;
            span{
              font: .75em/1.125em @sansregular;
            }
          }
        }
        .costumerInfo{
          margin-top: 60px;
          display: flex;
          flex-wrap: wrap;
          flex-direction: column;
          p {
            label {
              margin: 0 5px;
              position: relative;
              input {
                padding-left: 10px;
                width: 250px;
                height: 40px;
                font: 1em/1.125em @serifregular;
                color: #828282;
                outline: none;
                border: 2px solid #e6e6e6;
                border-radius: 5px;
              }
              .asterisk {
                position: absolute;
                top: 7px;
                right: 20px;
                font-size: .35em;
                color: #ff0000;
              }
            }
          }
          textarea{
            margin: 10px 0 30px 5px;
            padding: 10px;
            width: 514px;
            height: 90px;
            font: 1em/1.125em @serifregular;
            color: #828282;
            resize: none;
            border: 2px solid #e6e6e6;
            border-radius: 5px;
            outline: none;
          }
          button{
            width: 250px;
            height: 40px;
            font: .875em/40px @sansregular;
            color: #fff;
            text-align: center;
            text-transform: uppercase;
            background-color: @mainColor;
            border: none;
            border-radius: 5px;
            outline: none;
            &:hover{
              cursor: pointer;
            }
          }
        }
      }
      .content__howToMakeOrder {
        grid-area: order;
        margin: 95px 0;
        display: flex;
        flex-direction: column;
        div {
          margin: 10px 0;
          width: 100%;
          height: 235px;
          display: flex;
          flex-wrap: wrap;
          background-color: #e6e6e6;
          border-radius: 5px;
          .icons {
            width: 40%;
            display: flex;
            justify-content: flex-end;
            i {
              font-size: 2em;
              color: #1b6b77;
            }
            .arrow {
              margin-top: 30px;
              width: 20%;
              text-align: right;
            }
            .cart,
            .box,
            .truck {
              margin-top: 40px;
              width: 100px;
              height: 100px;
              display: flex;
              justify-content: center;
              align-items: center;
              border: 2px solid #1b6b77;
              border-radius: 75px;
            }
          }
          .learnMore {
            width: 60%;
            display: flex;
            justify-content: center;
            align-items: center;
            a {
              width: 165px;
              height: 45px;
              font: .875em/45px @sansregular;
              color: #bababa;
              text-align: center;
              text-decoration: none;
              border: 1px solid #bababa;
              border-radius: 5px;
              &:hover {
                color: #fff;
                background-color: #1b6b77;
                transition: .5s;
              }
            }
          }
          .info {
            margin-left: 110px;
            width: 100%;
            font: 1em/1.125em @sansregular;
            color: #474747;
            span:nth-child(1) {
              font: 1.125em/1.25em @sansbold;
            }
          }
        }
      }
    }
  }
  .contentAddres {
    display: flex;
    iframe {
      width: 60%;
      height: 600px;
    }
    .contentAddres__contactForm {
      width: 40%;
      display: flex;
      flex-wrap: wrap;
      background-color: #176975;
      .comentary {
        margin: 45px 0 0 45px;
        input {
          padding-left: 15px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
        }
        input[type*="text"] {
          margin: 0 5px 10px;
          width: 245px;
          height: 45px;
        }
        textarea {
          margin: 0 5px 10px;
          padding-left: 15px;
          width: 515px;
          height: 100px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
          resize: none;
        }
        button {
          margin-top: 20px;
          width: 165px;
          height: 45px;
          font: 1.125em/40px @sansregular;
          color: #fff;
          text-align: center;
          background: transparent;
          border: 1px solid #fff;
          border-radius: 10px;
          outline: none;
          &:hover {
            cursor: pointer;
          }
        }
      }
      .contacts {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        p {
          font: 1.125em/1.25em @sansregular;
          color: #fff;
          a {
            color: #fff;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      .social {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        ul {
          display: flex;
          justify-content: space-between;
          a {
            margin: 0 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 50px;
            font-size: 1.5em;
            color: #176975;
            text-decoration: none;
            background-color: #fff;
            border-radius: 75px;
          }
        }
        p {
          color: #fff;
          text-align: right;
          font: 1em/1.125em @sansbold;
          span:nth-child(1) {
            font: .875em/1em @sansregular;
          }
        }
      }
    }
  }
}