.authorization .author p .user,
.authorization .regist p .user,
.authorization .author p .shield,
.authorization .regist p .shield,
.authorization .author p .phone,
.authorization .regist p .phone,
.authorization .author p .envelope,
.authorization .regist p .envelope {
