  outline: none;
}
.authorization .author p .asterisk,
.authorization .regist p .asterisk {
  position: absolute;
  top: 20px;
  right: 85px;
  font-size: .35em;
  color: #ff0000;
}
.authorization .author p label .checkbox,
.authorization .regist p label .checkbox {
  display: none;
}
.authorization .author p label .checkbox-custom,
.authorization .regist p label .checkbox-custom {
  position: relative;
  width: 20px;
  height: 20px;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
